.container {
    display: flex;
    flex-direction: column;
    background-color: white;
    margin: auto;
    width: 600px;
    margin-top: 100px;
    padding-bottom: 30px;
    border-radius: 10px;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
    width: 100%;
}

.heading {
    color: orange;
    font-size: 3rem;
    font-weight: 600;
    margin-bottom: 10px;
}

.underline {
    width: 60px;
    height: 5px;
    border-radius: 10px;
   background-color: orange;
}

.input-box {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.input {
    display: flex;
    align-items: center;
    margin: auto;
    margin-bottom: 25px;
    width: 480px;
    height: 70px;
    border-radius: 5px;
    background: #D3D3D3;
}

.input img {
    margin: 0px 25px;
}

.input input {
    background: transparent;
    border: none;
    outline: none;
    font-size: 18px;
}

.forgot-password {
    padding-left: 10%;
    margin-top: 15px;
    font-size: 18px;
    color: #818080;
}

.forgot-password span {
    cursor: pointer;
    color: orange;
}

.footer {
    display: flex;
    justify-content: space-around;
    margin-top: 15px;
}

.button {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50px;
    width: 200px;
    height: 50px;
    font-size: 18px;
    font-weight: 600;
    color: white;
    background-color: orange;

}

.gray {
    background: #D3D3D3;
    color:#616060;
}